@import url('https://fonts.googleapis.com/css2?family=Akshar:wght@300;400&family=Bebas+Neue&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Press+Start+2P&display=swap');
* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
.timer {
box-sizing: border-box;
  background: linear-gradient(#19547b, #ffd89b);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.field{
  margin-right: 50px;
}
.field:nth-last-child(1){
  margin-right: 0;
}

.label {
  font-family: 'Bebas Neue',
  cursive;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 30px;
  filter: drop-shadow(0 0 5px #fff) drop-shadow(0 0 15px #fff);
}
.value {
  font-family: 'Press Start 2P',
  cursive;
  justify-content: center;
  align-items: center;
  text-align: center;
 line-height: 80px;
 font-size: 50px;
}
